html { box-sizing: border-box; }
*, ::before, ::after { box-sizing: inherit; }

body {
    margin: 0;
    font-size: 18px;
    font-family: sans-serif;
}

.site-header {
    margin: 20px;
}

.main {
    margin: 20px;
}

.logo {
    display: block;
    margin: 0 auto;
    max-width: 30vw;
}


.menu__nav {
    list-style: none;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.menu__item--home {
    display: none;
}

.menu__toggler {
    cursor: pointer;
    font-size: 48px;
    text-align: center;
    margin: 0 auto;
    display: block;
}
  


.menu__item {
   background-color: rgb(30, 29, 31);
   border-bottom: 1px solid white;
   border-right: 1px solid white;

   padding: 20px; 
}

.menu__link {
    text-decoration: none;
    display: block;
    color: white;

}

.menu__checkbox {
    position: absolute;
    left: -9999px;
}

.site-header {
    // display: flex;
    justify-content: space-evenly;

}

// @media screen and (max-width: 1009px) {
//     .menu__checkbox:checked ~ .menu__nav {
//         display: block;
//     }
// }

.slogan {
    text-align: center;
    color: #5812c1;;
    font-size: 32px;
    margin-top: 60px;
}

.product-card {
    max-width: 350px;
    // max-height: 300px;
    border: 1px solid black;
    margin: 80px auto;
}

.product-card:nth-child(1) {
    margin-top: 0px;
}

.product-card__image {
    max-width: 100%;
    border-bottom: 1px solid black;
}

.product-card__info {
    text-align: center;
    display: grid;
    grid-template-rows: 1fr 1fr;
    align-items: center;
    gap: 5px;

}

.product-card__button {
    text-align: center;
    background-color: #5812c1;
    margin: 0;
    padding: 10px;
}

.product-card__link {
    color: white;
    text-decoration: none;

}

.products-categories {
    margin: 50px 0;
}

.product-categories__category {
    width: 200px;
    height: 200px;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px auto;
    background-color: rgb(239, 238, 238);

}

.products-categories__descr {
    font-size: 32px;
    color: black;
}

.products-categories__link {
    text-decoration: none;
}

.product-categories__category:hover {
    border: 3px solid black;
}


.footer {
    max-height: 20%;
    color: rgb(240, 240, 240);
    background-color: rgb(24, 24, 24);
    margin: 0;
    padding: 20px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.footer-menu {
    list-style: none;
    margin: 0;
    padding: 0;
}

.footer-menu__item {
    margin: 15px;
    padding: 0;
}

.footer-menu__link {
    color: rgb(240, 240, 240);
    text-decoration: none;
}


@media screen and (min-width: 1010px) {
    .products-feed {
        display: grid;
        // grid-template-columns: repeat(5, 1fr);
        grid-template-columns: repeat(auto-fill, 350px);
        gap: 80px;
        justify-content: center;
        margin: 60px 0;
    }

    .menu__nav {
        display: flex;
        gap: 30px;
        justify-content: center;
        background-color: lightgray;
    }

    .menu__item {
        padding: 20px;
        background-color: lightgray;
        border: 0;
    }

    .menu__link {
        color: #222222;
    }

    .menu__item:hover {
        background-color: rgb(189, 189, 189);
    }

    .products-categories {
        display: grid;
        grid-template-columns: repeat(3, 225px);
        justify-content: center;
        grid-gap: 30px 50px;
    }

    .product-categories__category {
        margin: 0;
    }

    .menu__item--home {
        display: block;
    }

    .product-card {
        margin: 0;
    }

}




